            <td class="bb-table-column" :colspan="isCouponApplied ? 1 : 2">
              <div class="relative">
                <Input v-model="dataCoupon.code" :disabled="isCouponApplied" dense placeholder="Promo Code" block />
                <div v-show="isCouponApplied" class="absolute right-3 top-1/2 transform -translate-y-1/2">
                  <i class="material-icons text-green-04">check_circle</i>
                </div>
              </div>
              <small v-if="isCouponApplied" class="text-green-04 block mt-2">Promo code is valid</small>
            </td>
            <td v-show="isCouponApplied" class="bb-table-column text-right">
              <Currency :value="state.coupon_amount" />
            </td>
            <td class="bb-table-column">
              <transition name="slide-fade">
                <div v-if="isCouponChanged">
                  <Button @click="handleApplyCoupon" :loading="loadingCoupon.applyCoupon" class="mx-1" color="teal" dense>
                    <i class="material-icons text-lg">check</i>
                  </Button>
                  <Button @click="dataCoupon.code = dataCoupon.codeOriginal" :disabled="loadingCoupon.applyCoupon" class="mx-1" color="red" dense>
                    <i class="material-icons text-lg">close</i>
                  </Button>
                </div>
                <div v-else-if="isCouponApplied" class="group-hover:opacity-100 opacity-0 transition-all duration-200">
                  <Button @click="handleRemoveCoupon" :loading="loadingCoupon.removeCoupon" class="mx-1" color="red" dense>
                    <i class="material-icons text-lg">delete</i>
                  </Button>
                </div>
              </transition>
            </td>
          </tr>
        </template>
      </BBTable>
    </template>
  </Card>
  <Modal v-model="modal.delete" title="Delete Item" @close="unsetData">
    <p>Are you sure want to delete this item?</p>
    <template #footer>
      <Button color="teal" :loading="loading.delete" @click="deleteDevice">Delete Item</Button>
    </template>
  </Modal>
  <Modal v-model="modal.create" title="Add Item" @close="handleAddUnsetData">
    <template #default>
      <DeviceScan
        :show="modal.create"
        :selected="device.data"
        @scanned="handleScan"
      />
      <DeviceForm
        ref="refDeviceForm"
        v-model:data="device.data"
        v-model:dataComputer="device.computer"
        :device-scan="deviceScan"
        v-model:data-golf="device.golfSpecs"
        v-model:is-custom-color="device.data.is_custom_color"
        v-model:custom-color="device.data.custom_color"
        multi-imei
        buyback-type="instore"
        with-scan
      />
    </template>
    <template #footer>
      <div class="flex justify-between gap-x-4">
        <CancelAdd
          @cancel="() => {
            modal.create = false
            handleAddUnsetData()
          }"
        />
        <Button color="teal" :loading="loading.create" @click="addDevice">Add Item</Button>
      </div>
    </template>
  </Modal>
  <Modal v-model="modal.update" title="Update Item" @close="unsetData">
    <template #default>
      <DeviceForm
        ref="refDeviceForm"
        v-model:data="device.data"
        v-model:device="device.device"
        v-model:data-computer="device.computer"
        :device-scan="deviceScan"
        v-model:data-golf="device.golfSpecs"
        v-model:is-custom-color="device.data.is_custom_color"
        v-model:custom-color="device.data.custom_color"
        :golf-steps="device.data.golf_steps"
        :original-specs="device.original"
        :hide="{ qty: true }"
        buyback-type="instore"
        is-editable
        auto-fill
      />
    </template>
    <template #footer>
      <Button color="teal" :loading="loading.update" @click="updateDevice">Update</Button>
    </template>
  </Modal>
  <Modal v-model="modal.print_qr" title="Print Qr Label">
    <PrintLabel :data="printLabelData" :label-type="labelType" @close-modal="modal.print_qr = false" />
    <template #footer></template>
  </Modal>
  <Modal v-model="modal.print_barcode" title="Print Barcode Label">
      <PrintLabel :data="printLabelData" :label-type="labelType" @close-modal="modal.print_barcode = false" />
      <template #footer></template>
    </Modal>
  <Modal v-model="modal.phonecheck_info" :title="PhonecheckDevice?.product_name" @close="isErrorSelectedPhonecheck = false">
    <template #header>
      <div class="w-full flex justify-between items-center">
        <h5 class="modal__content-header-title">{{ PhonecheckDevice?.product_name }}</h5>
        <Button color="teal" @click="printPhonecheck(PhonecheckDevice)" :loading="loading.printPhonecheck" dense icon><i class="material-icons text-lg">print</i></Button>
      </div>
    </template>
    <template #default>
      <PhonecheckInfo ref="refPhonecheckInfo" v-model:data="PhonecheckInfoData" :is-error="isErrorSelectedPhonecheck" />
    </template>
  </Modal>
  <Modal v-model="modalBulkImeiAddDevice" size="xs" title="Add Bulk IMEI/SN" @close="handleCloseAddBulkImeiModal">
    <template #default>
      <div class="flex bg-orange-lighten text-orange-04 p-4 rounded-lg mb-4">
        <div class="mr-4">
          <i class="material-icons">info</i>
        </div>
        <div class="mt-1">
          IMEIs/SNs entered here will overwrite those already input on the Quote Details page for these items.
        </div>
      </div>
      <BulkImeiSNInput ref="refBulkImeiSNInput" v-model="bulkImei" :qty="dataBulkImeiAddDevice?.qty" block rows="5" />
    </template>
    <template #footer>
      <div class="flex justify-between items-center">
        <span @click="handleCloseAddBulkImeiModal" class="text-red-04 cursor-pointer px-2">Cancel</span>
        <Button @click="handleAddBulkImeiDevice" :loading="loadingBulkImeiAddDevice" :disabled="!bulkImei" color="teal" dense>
          <span class="px-2">Add</span>
        </Button>
      </div>
    </template>
  </Modal>
  <BulkCondition :loading="bulkCondition.loading" :show="bulkCondition.modal" @close="handleCloseModalBulkCondition" :items="selectedItems" :conditions="itemConditions" :loadingCondition="loadingCondition" :me="me" @submit="handleBulkChangeItemCondition" />
</template>

<script>
import { computed, defineComponent, ref, nextTick, watch, onUnmounted, reactive } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'
import { useUser } from 'composable/user'
import { useShowItems, useShow, useBulkImeiAddDevice, useBulkCondition, resetScanQR, removeScanQR } from 'composable/buyback'
import { useCondition } from 'composable/device'
import { useAll } from 'composable/integration'
import { useGolfSpec } from 'composable/golf'

import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import DeviceForm from 'components/Device/Form.vue'
// import NewDeviceForm from 'components/Device/DeviceForm.vue'
import PrintLabel from 'components/PrintLabel.vue'
import Currency from 'components/Currency.vue'
import PhonecheckInfo from 'components/Phonecheck/Info.vue'
import BBTable from 'components/Table/BBTable.vue'
import BBTColumn from 'components/Table/BBTColumn.vue'
import { currency, money } from 'utils/number'
import { deepClone } from 'utils/deep-clone'
import debounce from 'utils/debounce'
import { labelDataBuilder } from 'utils/dymo'
import { ElMessage } from 'element-plus'
import BulkCondition from 'components/Buyback/Condition/BulkCondition.vue'
import BBBScan from 'components/BuybackBoothScan/index.vue'
import DeviceScan from 'components/DeviceScan.vue'
import CancelAdd from 'components/BuybackBoothScan/CancelAdd.vue'
import BulkImeiSNInput from 'components/BulkImeiSN/Input.vue'
import GolfSpecBadge from 'components/Device/DeviceGolfSpecBadge.vue'

export default defineComponent({
  components: {
    Input,
    Button,
    Modal,
    Card,
    DeviceForm,
    DeviceScan,
    // NewDeviceForm,
    PrintLabel,
    BBBScan,
    BBTable,
    BBTColumn,
    Currency,
    PhonecheckInfo,
    BulkCondition,
    Badge,
    CancelAdd,
    BulkImeiSNInput,
    GolfSpecBadge
  },
  async setup() {
    const store = useStore()
    const { state, device, modal, selected, dataCoupon, loadingCoupon, isCouponApplied, isCouponChanged, unsetData, openModal, actionAll, actionCreate, actionUpdate, actionDelete, applyCoupon, removeCoupon } = useShowItems('local')
    const { state: stateShow, action: actionShow } = useShow('local')
    const { loading, actionImeiCheck, actionImeiCheckPrint, actionImeiCheckPrintBulk, actionEmailPhonecheck } = useShowItems('online')
    const { stateEnabledIntegration, actionEnabledIntegration } = useAll()
    const { data: dataBulkImeiAddDevice, action: actionBulkImeiAddDevice, loading: loadingBulkImeiAddDevice, modal: modalBulkImeiAddDevice, unsetData: unsetDataBulkImeiAddDevice } = useBulkImeiAddDevice()
    const { update: updateBulkCondition } = useBulkCondition('online')
    const { loading: loadingCondition, items: itemConditions, action: actionCondition, actionCustom: actionCustomConditions } = useCondition()
    const { transformSeperateCustomSpecs } = useGolfSpec()

    const { me } = useUser()
    const route = useRoute()
    const refDeviceForm = ref(null)
    const refBulkImeiSNInput = ref(null)
    // const refNewDeviceForm = ref(null)
    const printLabelData = ref([])
    const selectedItems = ref([])
    const refPhonecheckInfo = ref(null)
    const refItemTable = ref(null)
    const PhonecheckInfoData = ref(null)
    const PhonecheckDevice = ref(null)
    const isErrorSelectedPhonecheck = ref(false)
    const validPhonecheckselectedItems = ref([])
    const sameProduct = ref(false);
    const labelType = ref('qr')
    const isReadonly = computed(() => stateShow.value && stateShow.value?.is_readonly)
    const hasEnablePhonecheck = computed(() => me.value.package_id >= 4 && stateEnabledIntegration.value.is_phonecheck_enable == 1)
    const isBulkImei = ref(false)
    device.marketerId = route.params.marketerId

    const items = computed(() => {
      // NOTE: Do deep copy for now, IDK Vuex keep warning about mutating state outside mutation
      return JSON.parse(JSON.stringify(state.value))
    })
    const bulkImei = computed({
      set(newImeiData){
        dataBulkImeiAddDevice.imei = newImeiData ? newImeiData.split('\n') : null
      },
      get(){
        return Array.isArray(dataBulkImeiAddDevice.imei) ? dataBulkImeiAddDevice.imei.join('\n') : dataBulkImeiAddDevice.imei
      }
    })
    const deviceScan = computed(() =>
      store.state &&
      store.state.user &&
      store.state.user.bbbScan &&
      store.state.user.bbbScan.device
    )

    const viewPhoneCheckData = (data) => {
      PhonecheckInfoData.value = JSON.parse(data.phonecheck_info)
      PhonecheckDevice.value = data
      openModal('phonecheck_info');
    }

    const importPhoneCheck = async () => {
      let payload = selectedItems.value.map(item => {
        return {
          id: item.id,
          imei_sn: item.imei_sn
        }
      })
      await actionImeiCheck(route.params.buybackId, {
        imei: payload,
        origin: 'mail-in'
      })
      await actionAll(route.params.buybackId)
    }

    const importSinglePhoneCheck = async (item) => {
      let payload = [{
          id: item.id,
          imei_sn: item.imei_sn
      }];

      let response = await actionImeiCheck(route.params.buybackId, {
        imei: payload,
        origin: 'mail-in'
      })

      if (response.data.length == 0 && response?.is_phonecheck_error !== 1) {
        return ElMessage({
          message: 'IMEI/SN not found',
          type: 'warning'
        })
      } else {
        PhonecheckInfoData.value = response.data[0]?.info;
        PhonecheckDevice.value = item
        if (response.data.length && !response.is_phonecheck_error) {
          isErrorSelectedPhonecheck.value = false
        } else {
          isErrorSelectedPhonecheck.value = response.is_phonecheck_error ? response.is_phonecheck_error == 1 : true
        }
        openModal('phonecheck_info');
      }

      await actionAll(route.params.buybackId)
    }

    const isExistAllPhonecheck = computed(() => {
      return selectedItems.value.every(item => item.phonecheck_status == 'exists')
    })

    const printPhonecheck = async (data) => {
      await actionImeiCheckPrint(route.params.buybackId, {
        id: data.id,
        imei: data.imei_sn,
        origin: 'mail-in'
      })
    }

    const sendEmailPhonecheck = async (data, isBulk=false) => {
      const payload = {
        customer_email: stateShow.value?.customer?.email,
        box_id: route.params.buybackId,
        imei: isBulk ? selectedItems.value.map(item => {
          return {
            id: item.id,
            imei_sn: item.imei_sn
          }
        }) : [
          {
            id: data.id,
            imei_sn: data.imei_sn
          }
        ]
      }

      try {
        const response = await actionEmailPhonecheck(payload)
        
        ElMessage({
          message: response.message,
          type: 'success'
        })
      } catch (error) {
        console.log(error)
      }
    }

    const importBulkPhoneCheck = async () => {
      await actionImeiCheckPrintBulk(route.params.buybackId, {
        ids: validPhonecheckselectedItems.value.map((item) => item.id),
        origin: 'mail-in'
      })
    }

    const imeiUpdate = debounce(async (data) => {
      await actionUpdate(route.params.buybackId, data.id,  {
        imei_sn: data.imei_sn,
        notes: data.notes
      })
    }, 300)

    const selectItems = (items) => {
      selectedItems.value = items;
      validPhonecheckselectedItems.value = items.filter((item) => item.phonecheck_status == 'exists')
      checkSameProduct();
    }

    const checkSameProduct = () => {
      sameProduct.value = false;
      if (selectedItems.value.length > 0) {
        sameProduct.value = true;
        const isMac = selectedItems.value[0].is_mac;
        const categoryId = selectedItems.value[0].category_id;

        selectedItems.value.forEach(item => {
          if (item.is_mac != isMac) sameProduct.value = false;
          if (item.category_id != categoryId) sameProduct.value = false;
        });
      }
    }

    const rowKey = row => {
      const child = row.child ? '_parent' : '_child'
      return row.id + child
    }
    const isParent = (row, index) => {
      return !(row.child && row.child.length > 0)
    }
    const printQrLabel = (data) => {
      const label = labelDataBuilder({
        id: data.id,
        productName: data.product_name,
        isMac: data.is_mac,
        detail: data.detail,
        condition: data.condition,
        buybackId: route.params.buybackId,
        imei: data.imei_sn
      })

      labelType.value = 'qr'
      printLabelData.value = [label]
      modal.print_qr = true
    }

    const printBarcodeLabel = (data) => {
      const label = labelDataBuilder({
        id: data.id,
        productName: data.product_name,
        isMac: data.is_mac,
        detail: data.detail,
        condition: data.condition,
        buybackId: route.params.buybackId,
        imei: data.imei_sn,
        price: currency(me.value, data.price)
      })

      labelType.value = 'barcode'
      printLabelData.value = [label]
      modal.print_barcode = true
    }

    const printQrLabelBatch = () => {
      const labels = []
      for(let item of selectedItems.value) {
        labels.push(labelDataBuilder({
          id: item.id,
          productName: item.product_name,
          isMac: item.is_mac,
          detail: item.detail,
          condition: item.condition,
          buybackId: route.params.buybackId,
          imei: item.imei_sn
        }))
      }

      labelType.value = 'qr'
      printLabelData.value = labels
      modal.print_qr = true
    }

    const printBarcodeLabelBatch = () => {
      const labels = []
      for(let item of selectedItems.value) {
        labels.push(labelDataBuilder({
          id: item.id,
          productName: item.product_name,
          isMac: item.is_mac,
          detail: item.detail,
          condition: item.condition,
          buybackId: route.params.buybackId,
          imei: item.imei_sn,
          price: currency(me.value, item.price)
        }))
      }

      labelType.value = 'barcode'
      printLabelData.value = labels
      modal.print_barcode = true
    }

    const deviceTableUpdate = async (data) => {
      await actionUpdate(route.params.buybackId, data.id,  {
        imei_sn: data.imei_sn,
        notes: data.notes
      })
    }
    const deleteDevice = async (id, data) => {
      await actionDelete(route.params.buybackId)
    }
    const updateDevice = async () => {
      const isValid = await nextTick(() => refDeviceForm.value.validate())
      if( isValid ){
        const deviceData = {
        ...device.data
        }
        Object.assign(deviceData, {
          product_id: device.data.product_id ? device.data.product_id + (device.computer.processor ? `-${device.computer.processor.id}-${device.computer.memory.id}-${device.computer.hardDrive.id}-${device.computer.graphicsCard.id}` : '') : -1,
          processor: device.computer.processor?.name || null,
          hard_drive: device.computer.hardDrive?.name || null,
          memory: device.computer.memory?.name || null,
          graphics: device.computer.graphicsCard?.name || null,
          device: device.device?.detail?.device || null,
          model: device.device?.detail?.model || null,
          network: device.device?.detail?.network || null,
          size: device.device?.detail?.size || null,
          is_custom: device.data.is_custom || 0,
          notes: device.data.notes,
          buyback_type: device.data.buyback_type || 'instore',
          ...(transformSeperateCustomSpecs(device.golfSpecs)),
          ...(device.data.is_custom_color
            ? {
                custom_color: device.data.custom_color,
                color_id: null
              }
            : {
                color_id: device.data.color_id
              })
        })
        await actionUpdate(route.params.buybackId, device.id, deviceData, async () => {
          await actionAll(route.params.buybackId)
        })
      }
    }
    const openModalSelected = (target, item) => openModal(target, () => {
      if( target === 'update' ){
        device.id = item.id
        Object.assign(device.data, {
          product_name: item.product_name,
          product_id: item.product_id,
          category_id: item.category_id,
          category_name: item.category_name,
          condition: item.condition,
          is_mac: item.is_mac,
          is_custom: item.is_custom,
          imei_sn: item.imei_sn,
          imei_required: item.imei_required,
          price: item.price,
          qty: item.qty,
          color: item.color,
          color_id: item.color_id,
          notes: item.notes,
          golf_steps: item.golf_step,
          is_custom_color: item.custom_color !== null,
          custom_color: item.custom_color || null
        })
        Object.assign(device.original, {
          is_mac: item.is_mac || 0,
          product_name: item.ori_product_name || '',
          processor: item.ori_processor_speed || '',
          hard_drive: item.ori_hard_drive || '',
          memory: item.ori_memory || '',
          graphics: item.ori_graphics || '',
          created_at: item.created_at
        })
        Object.assign(device.golfSpecs, {
          length: {
            selected: item.golf_length || item.custom_length || null,
            is_custom: item.custom_length !== null
          },
          loft: {
            selected: item.golf_loft || item.custom_loft || null,
            is_custom: item.custom_loft !== null
          },
          gender: {
            selected: item.golf_gender || item.custom_gender || null,
            is_custom: item.custom_gender !== null
          },
          shaft_model: {
            selected: item.golf_shaft_model || item.custom_shaft_model || null,
            is_custom: item.custom_shaft_model !== null
          },
          shaft_material: {
            selected: item.golf_shaft_material || item.custom_shaft_material || null,
            is_custom: item.custom_shaft_material !== null
          }
        })
      }
      selected.value = item.id
    })
    const addDevice = async () => {
      const isValid = await nextTick(() => refDeviceForm.value.validate())
      if(isValid) {
        if (store.state.user.bbbScan && store.state.user.bbbScan.device) {
          device.data['bbb_diagnostic_result'] = JSON.stringify(store.state.user.bbbScan.device)
        }
        device.data = {
          ...device.data,
          ...(transformSeperateCustomSpecs(device.golfSpecs)),
          ...(device.data.is_custom_color
            ? {
                custom_color: device.data.custom_color,
                color_id: null
              }
            : {
                color_id: device.data.color_id
              }),
					questions: device.data.questions.reduce((previousValue, currentValue) => {
						if (typeof currentValue.answer_id === 'object') {
							currentValue.answer.forEach((answer) => previousValue.push({
								question_id: currentValue.question_id,
								answer_id: answer.id
							}))
						}
						else if (!!currentValue.answer_id) previousValue.push({
							question_id: currentValue.question_id,
							answer_id: currentValue.answer.id
						})

						return previousValue
					}, [])
        }
        await actionCreate(route.params.buybackId)
        removeScanQR()
      }
    }
    const tableSummary = ({columns}) => {
      return columns.map(column => column.property === 'total' ? `${currency(me.value, state.value.total)}` : '' || column.property === 'price' ? 'Total' : '')
    }
    const handleOpenAddBulkImeiModal = (item) => {
      const { product_id, product_name, condition, price, qty } = item
      dataBulkImeiAddDevice.product_id = product_id,
      dataBulkImeiAddDevice.product_name = product_name
      dataBulkImeiAddDevice.condition = condition
      dataBulkImeiAddDevice.price = price
      dataBulkImeiAddDevice.qty = qty
      const result = []
      item?.child?.length && item.child.forEach(child => {
        result.push(child?.imei_sn || '')
      })
      dataBulkImeiAddDevice.imei = result

      isBulkImei.value = true
      modalBulkImeiAddDevice.value = true
    }
    const handleCloseAddBulkImeiModal = () => {
      isBulkImei.value = false
      modalBulkImeiAddDevice.value = false
      refBulkImeiSNInput?.value?.validate.$reset()
      unsetDataBulkImeiAddDevice()
    }
    const handleAddBulkImeiDevice = async () => {
      if (!await refBulkImeiSNInput.value.validate.$validate()) return

      await actionBulkImeiAddDevice(route.params.buybackId, async () => {
        handleCloseAddBulkImeiModal()
        await Promise.all([
          actionAll(route.params.buybackId),
          actionShow(route.params.buybackId)
        ])
        ElMessage({
          message: 'Item IMEI added',
          type: 'success'
        })
      })
    }
    const handleAddUnsetData = () => {
      unsetData()
      resetScanQR()
    }
    const handleScan  = (val) => {
      refDeviceForm.value.stateSearch.query = val ? val.Name : null
      refDeviceForm.value.stateSearch.product.query = val ? val.Name : null
      if (val) {
        refDeviceForm.value.imeiSnNotes[0] = {
          imei_sn: val.UID,
          imei_notes: ''
        }
        if (refDeviceForm.value.refDeviceSearch) {
          refDeviceForm.value.refDeviceSearch.isCollapse = true
        }
      }
      refDeviceForm.value.handleSearchProduct()
    }

    const stopWatcher = watch(() => route.params.buybackId, debounce(async (newBubyackId, oldBuybackId) => {
      if(newBubyackId && oldBuybackId) {
        await actionEnabledIntegration()
        await actionAll(route.params.buybackId)
      }
    }))
    onUnmounted(() => stopWatcher())

    const bulkCondition = reactive({
      modal: false,
      loading: false
    })

    const handleBulkChangeCondition = async () => {
      bulkCondition.modal = true;

      const isCustomProduct = selectedItems.value.length > 0 ? selectedItems.value[0].is_custom == 1 : false
      if(isCustomProduct) {
        await actionCustomConditions({
          category_id: selectedItems.value[0].category_id,
          is_mac: selectedItems.value[0].is_mac
        })
      }else {
        await actionCondition({
          product_id: selectedItems.value[0].product_id,
          is_mac: selectedItems.value[0].is_mac,
        })
      }
    }

    const handleCloseModalBulkCondition = () => {
      bulkCondition.modal = false;
    }

    const handleBulkChangeItemCondition = async (data) => {
      await updateBulkCondition({
        boxId: route.params.buybackId,
        data: data,
      })

      ElMessage({
        type: 'success',
        message: 'Item conditions updated'
      })
      bulkCondition.modal = false;
      selectedItems.value = []
      refItemTable.value.checkValue = []

      actionAll(route.params.buybackId)
    }

    await actionEnabledIntegration()
    await actionAll(route.params.buybackId)
    await actionShow(route.params.buybackId)

    const handleChangeDeviceForm = (item) => {
      unsetData()
      const isCustomDevice = item.product.is_custom === 1
      if(isCustomDevice){
        Object.assign(device.data, {
          product_id: -1,
          product_name: item.customDevice.product_name,
          qty: item.customDevice.qty,
          price: item.customDevice.price,
          condition_id: -1,
          condition: item.customDevice.condition,
          buyback_type: item.product.buyback_type,
          notes: JSON.stringify(['']),
          imei_sn: JSON.stringify(['']),
          is_custom: 1,
          is_mac: 0
        })
        device.data.product_id = -1
        device.data.product_name = item.customDevice.product_name
        device.data.qty = item.customDevice.qty
        device.data.price = item.customDevice.price
        device.data.condition = item.customDevice.condition
        return
      }
      device.data = item.product
      device.computer = item.specs
    }
    const handleApplyCoupon = async () => {
      await applyCoupon({ boxId: route.params.buybackId }, async (err, response) => {
        if (err) return
        await actionAll(route.params.buybackId)
      })
    }
    const handleRemoveCoupon = async () => {
      await removeCoupon({boxId: route.params.buybackId}, async (err, response) => {
        if(err) return
        await actionAll(route.params.buybackId)
      })
    }

    return {
      me,
      device,
      deviceScan,
      state,
      stateShow,
      items,
      loading,
      modal,
      loadingBulkImeiAddDevice,
      loadingCoupon,
      modalBulkImeiAddDevice,
      printLabelData,
      unsetData,
      isBulkImei,
      bulkImei,
      dataBulkImeiAddDevice,
      dataCoupon,
      rowKey,
      isParent,
      refDeviceForm,
      refBulkImeiSNInput,
      // refNewDeviceForm,
      openModalSelected,
      deviceTableUpdate,
      addDevice,
      deleteDevice,
      updateDevice,
      openModal,
      tableSummary,
      printQrLabel,
      printBarcodeLabel,
      printQrLabelBatch,
      printBarcodeLabelBatch,
      selectItems,
      selectedItems,
      money,
      refPhonecheckInfo,
      PhonecheckInfoData,
      PhonecheckDevice,
      isErrorSelectedPhonecheck,
      isCouponApplied,
      isCouponChanged,
      validPhonecheckselectedItems,
      isReadonly,
      hasEnablePhonecheck,
      viewPhoneCheckData,
      importPhoneCheck,
      importSinglePhoneCheck,
      printPhonecheck,
      importBulkPhoneCheck,
      imeiUpdate,
      handleOpenAddBulkImeiModal,
      handleCloseAddBulkImeiModal,
      handleAddBulkImeiDevice,
      handleAddUnsetData,
      handleScan,
      handleBulkChangeCondition,
      bulkCondition,
      handleCloseModalBulkCondition,
      handleBulkChangeItemCondition,
      refItemTable,
      sameProduct,
      loadingCondition,
      itemConditions,
      handleChangeDeviceForm,
      handleApplyCoupon,
      handleRemoveCoupon,
      labelType,
      isExistAllPhonecheck,
      sendEmailPhonecheck
    }
  }
})
</script>
